/* import tailwind */
@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";

body {
  width: 100%;
  min-width: 100vw;
  height: 100%;
  min-height: 100vh;
}

html {
  transition: all 200ms ease-in-out;
}

footer .footer {
  height: 300px;
}

.primary .brand {
  width: 30vw;
  margin-left: 1vw;
}


.container {
  padding-left: 20px;
  padding-right: 20px;
  max-width: 1900px !important;
}

.head-col,
.container-sm {
  padding-left: 8vw;
  padding-right: 8vw;
  max-width: 1200px !important;
}

.head-col {
  @apply flex flex-auto mx-auto w-full;
}

.menu-guide {
  left: 0;
  top: 0;
  margin-top: calc(5vw - 5px);
  visibility: hidden;
}

hr.linerule {
  margin-right: 55px;
}

.dropdown-menu {
  top: 0;
  margin-top: 5.5vw;
}

.dropdown-menu > hr {
  margin-right: 55px;
}

.heading-branded-text {
  width: 100%;
  max-width: 560px;
  text-align: left;
}

.menu-link:hover .menu-guide {
  visibility: visible !important;
}

.menu-link:hover+.dropdown-menu {
  visibility: visible !important;
}

.primary-navigation .dropdown-menu:hover {
  visibility: visible !important;
}

.heading {
  height: 82vh;
}
